console.log('lessons form');

import { timeSlots, lessons } from './constants';
const bookBtn = document.querySelector('#bookBtn');
const formLessons = document.querySelector('#formLessons');

const lesson = {};
let login = JSON.parse(localStorage.getItem('login'));

bookBtn.addEventListener('click', (event) => {
    event.preventDefault();
    let inpType = formLessons.querySelector('input[name="type"]:checked');
    let inpTime = formLessons.querySelector('input[name="time"]:checked');
    const type = lessons[inpType.id];
    const time = timeSlots[inpTime.id];
    const key = 'lessons';

    let tomorrow = false;

    if (inpTime.id === 'time_04' || inpTime.id === 'time_05' || inpTime.id === 'time_06') {
        tomorrow = true;
    }


    lesson.name = login.name,
        lesson.time = time,
        lesson.tomorrow = tomorrow,
        lesson.title = type.title,
        lesson.duration = type.duration;

    console.log(lesson);
    if (localStorage.getItem(key)) {
        const dataFromLS = localStorage.getItem(key);
        const parsedData = JSON.parse(dataFromLS);
        parsedData.push(lesson);
        localStorage.setItem(key, JSON.stringify(parsedData));
    } else {
        localStorage.setItem(key, JSON.stringify([lesson]));
    }

});